var canDoEvent = true;
var util = {
    KeyboardEventHandler_onKeyEvent: function(key, e, react, page){
        console.log("start Key Event >> ",key, canDoEvent)

        if (canDoEvent){
            canDoEvent = false;
            var jqTarget = window.$( e.target )
            var jqVisibleInputs = window.$('a:visible,:input:visible,div[tabindex]');
            var jqFirst = jqVisibleInputs.first();
            var jqLast = jqVisibleInputs.last();

            switch(key){
                //case 'left':
                /*case 'z':
                    let l_row = parseInt(jqTarget.attr('row_index'))
                    let l_col = parseInt(jqTarget.attr('col_index'))-1
                    let l_col_max = parseInt(jqTarget.attr('col_max'))

                    if (l_col  < 0 ) l_col = l_col_max-1
                    let l_child = window.$('div[row_index='+l_row+'][col_index='+l_col+']')[0]
                    console.log("to >>",l_col)
                    //ccl_col++;
                    let l_space = (
                        parseInt(window.$(l_child).css('width').replace('px','')) +
                        parseInt(window.$(l_child).css('margin-right').replace('px',''))
                    ) ;
                    let l_p_width = l_space * window.$(l_child.parentNode).find('div[col_index]').length +
                            (window.$(l_child.parentNode).find('div[col_index]').length > 1 ? 55.14+80 : 0 )

                    let l_scrollLeft = Math.round(l_child.parentNode.scrollLeft)

                    console.log(l_child.parentNode.scrollLeft, "l_scrollLeft",l_scrollLeft, "l_space",l_space, "l_p_width",l_p_width,"col_index_length",window.$(l_child.parentNode).find('div[col_index]').length)
                    console.log((l_scrollLeft - l_space < 0), l_p_width - l_space - (55.14+80) , l_scrollLeft - l_space)
                    let l_toLeft = (l_scrollLeft - l_space < 0) ? l_p_width - l_space - (55.14+80) : l_scrollLeft - l_space;
                    //l_child.parentNode.scrollLeft = l_toLeft

                    console.log("do left >> ",key, l_toLeft)
                    window.$(l_child.parentNode).animate({scrollLeft: l_toLeft},"normal","linear",()=>{
                        canDoEvent = true;
                        console.log("left jquery animate finish")
                    });

                    l_child.focus();

                    /*setTimeout(()=>{
                        if (l_child.parentNode.scrollLeft !== l_toLeft ){
                            l_child.parentNode.scrollLeft = l_toLeft
                        }
                    },100)* /

                    /*if (jqTarget.is(jqFirst))
                        jqLast.focus()
                    else
                        jqTarget.prev().focus();* /
                    page.resetCounter();
                    break;*/
                case 'z':
                    let l_row = parseInt(jqTarget.attr('row_index'))
                    let l_col = parseInt(jqTarget.attr('col_index'))-1
                    let l_col_max = parseInt(jqTarget.attr('col_max'))

                    if (l_col  < 0 ) l_col = l_col_max-1
                    let l_child = window.$('div[row_index='+l_row+'][col_index='+l_col+']')[0]

                    console.log("to >>",l_col)
                    //ccl_col++;
                    let l_space = (
                        parseInt(window.$(l_child).css('width').replace('px','')) +
                        parseInt(window.$(l_child).css('margin-right').replace('px',''))
                    ) ;

                    let l_toLeft = l_space * l_col

                    window.$(l_child.parentNode).animate({scrollLeft: l_toLeft},"normal","linear",()=>{
                        canDoEvent = true;
                        console.log("left jquery animate finish")
                    });

                    l_child.focus();
                    page.resetCounter();
                    break;
                //case 'right':
                case 'c':
                    let r_row = parseInt(jqTarget.attr('row_index'))
                    let r_col = parseInt(jqTarget.attr('col_index'))+1
                    let r_col_max = parseInt(jqTarget.attr('col_max'))

                    if (r_col === r_col_max) r_col = 0
                    let r_child = window.$('div[row_index='+r_row+'][col_index='+r_col+']')[0]
                    let r_space = (
                        parseInt(window.$(r_child).css('width').replace('px','')) +
                        parseInt(window.$(r_child).css('margin-right').replace('px',''))
                    )

                    let r_toLeft = r_space*r_col

                    window.$(r_child.parentNode).animate({scrollLeft: r_toLeft},"normal","linear",()=>{
                        canDoEvent = true;
                        console.log("right jquery animate finish")
                    });

                    r_child.focus();
                    page.resetCounter();
                    break;
                /*case 'c_':
                    let r_row = parseInt(jqTarget.attr('row_index'))
                    let r_col = parseInt(jqTarget.attr('col_index'))+1
                    let r_col_max = parseInt(jqTarget.attr('col_max'))

                    if (r_col === r_col_max) r_col = 0
                    let r_child = window.$('div[row_index='+r_row+'][col_index='+r_col+']')[0]
                    let r_space = (
                        parseInt(window.$(r_child).css('width').replace('px','')) +
                        parseInt(window.$(r_child).css('margin-right').replace('px',''))
                    ) ;//* r_col;
                    let r_p_width = r_space * window.$(r_child.parentNode).find('div[col_index]').length //+

                    let r_scrollLeft = Math.round(r_child.parentNode.scrollLeft)

                    console.log("to >>",r_col)
                    console.log(r_child.parentNode.scrollLeft, "r_scrollLeft", r_scrollLeft, "r_space",r_space, "r_p_width",r_p_width,"col_index_length",window.$(r_child.parentNode).find('div[col_index]').length)
                    console.log((r_scrollLeft + r_space >= r_p_width), 0 , r_scrollLeft + r_space)

                    let r_toLeft = (r_scrollLeft + r_space >= r_p_width) ? 0 : r_scrollLeft + r_space;

                    console.log("do right >> ",key, r_toLeft)
                    window.$(r_child.parentNode).animate({scrollLeft: r_toLeft},"normal","linear",()=>{
                        canDoEvent = true;
                        console.log("right jquery animate finish")
                    });
                    //r_child.parentNode.scrollLeft = r_toLeft
                    r_child.focus();

                / * setTimeout(()=>{
                        if (r_child.parentNode.scrollLeft !== r_toLeft ){
                            r_child.parentNode.scrollLeft = r_toLeft
                        }
                    },100)*/
                    /*if (jqTarget.is(jqLast))
                        jqFirst.focus()
                    else
                        jqTarget.next().focus();* /
                    page.resetCounter();
                    break;*/
                case 'enter':
                    if (jqTarget.attr('attr') != null){
                        react.setState( JSON.parse(jqTarget.attr('attr')) )
                    }
                    if (jqTarget.attr('selectcallback') != null){
                        page[jqTarget.attr('selectcallback')]( jqTarget.attr('selectcallbackparam'))
                    }
                    canDoEvent = true;
                    if (jqTarget.attr('targetpage')){
                        react.changePage( parseInt(jqTarget.attr('targetpage')))
                    } else {
                        react.changePage( react.state.page+1 )
                    }
                    break;
                //case 'up':
                case 's':
                    //debugger
                    if (jqTarget[0].tagName === "DIV"){
                        let u_row = parseInt(jqTarget.attr('row_index')) - 1
                        let u_row_max = parseInt(jqTarget.attr('row_max'))
                        let u_col = parseInt(jqTarget.attr('col_index'))

                        if ( u_row  < 0)
                            u_row = u_row_max-1
                        let u_next = window.$('div[row_index='+u_row+'][col_index=0]');

                        while( u_next.length === 0){
                            u_row = u_row - 1
                            if ( u_row  < 0) u_row = u_row_max-1
                            u_next = window.$('div[row_index='+u_row+'][col_index=0]');
                        }

                        let u_col_current = window.$('div[row_index='+(u_row)+'][row_header=true]').attr('col_current')
                        u_col = ( u_col_current ) ? u_col_current  : 0

                        //if (u_next.length > 0){
                        /*let u_col_max = window.$(u_next[0]).attr('col_max')
                        if (u_col > u_col_max-1)
                            u_col = u_col_max-1*/
                        //}

                        let u_child = window.$('div[row_index='+u_row+'][col_index='+u_col+']')[0]
                        /*u_child.parentNode.scrollLeft = (
                            parseInt(window.$(u_child).css('width').replace('px','')) +
                            parseInt(window.$(u_child).css('margin-right').replace('px',''))
                        ) * u_col*/
                        let u_space = (
                            parseInt(window.$(u_child).css('width').replace('px','')) +
                            parseInt(window.$(u_child).css('margin-right').replace('px',''))
                        ) * u_col;

                        //u_child.parentNode.scrollLeft = u_space
                        window.$(u_child.parentNode).animate({scrollLeft: u_space},"normal","linear",()=>{
                            canDoEvent = true;
                            console.log("up jquery animate finish")
                        });

                        u_child.focus();
                    } else {
                        if (jqTarget.is(jqFirst))
                            jqLast.focus()
                        else
                            jqTarget.prev().focus();
                    }

                    page.resetCounter();
                    break;
                //case 'down':
                case 'x':
                    //debugger
                    if (jqTarget[0].tagName === "DIV"){
                        let d_row = parseInt(jqTarget.attr('row_index')) +1
                        let d_row_max = parseInt(jqTarget.attr('row_max'))
                        let d_col = parseInt(jqTarget.attr('col_index'))

                        if ( d_row === d_row_max)
                            d_row = 0;
                        let d_next = window.$('div[row_index='+d_row+'][col_index=0]');
                        while( d_next.length === 0){
                            d_row = d_row+1
                            if ( d_row === d_row_max)  d_row = 0;
                            d_next = window.$('div[row_index='+d_row+'][col_index=0]');
                        }

                        let d_col_current = window.$('div[row_index='+(d_row)+'][row_header=true]').attr('col_current')
                        d_col = ( d_col_current ) ? d_col_current  : 0

                        //if (d_next.length > 0){
                    /* let d_col_max = window.$(d_next[0]).attr('col_max')
                        if (d_col > d_col_max-1)
                            d_col = d_col_max-1*/
                        //}

                        let d_child = window.$('div[row_index='+d_row+'][col_index='+d_col+']')[0]
                        /*d_child.parentNode.scrollLeft = (
                            parseInt(window.$(d_child).css('width').replace('px','')) +
                            parseInt(window.$(d_child).css('margin-right').replace('px',''))
                        ) * d_col */

                        let d_space = (
                            parseInt(window.$(d_child).css('width').replace('px','')) +
                            parseInt(window.$(d_child).css('margin-right').replace('px',''))
                        ) * d_col;
                        //let d_p_width = d_space * window.$(d_child.parentNode).find('div[col_index]').length

                        //let d_scrollLeft = d_child.parentNode.scrollLeft
                        //let r_toLeft = (r_scrollLeft + r_space >= r_p_width) ? 0 : r_scrollLeft + r_space;

                        window.$(d_child.parentNode).animate({scrollLeft: d_space},"normal","linear",()=>{
                            canDoEvent = true;
                            console.log("down jquery animate finish")
                        });
                        //d_child.parentNode.scrollLeft = d_space

                        d_child.focus();
                    } else {
                        if (jqTarget.is(jqLast))
                            jqFirst.focus()
                        else
                            jqTarget.next().focus();
                    }
                    page.resetCounter();
                    break;
                case 'esc':
                    if (react.state.page > 1){
                        canDoEvent = true;
                        react.changePage( react.state.page-1)
                    }
                    break;
                default:
                    canDoEvent = true;
                    break;
            }
        }

        console.log("end Key Event >> ",key, canDoEvent)
   }
}

exports.Util = util;